/*-------- PAGEWIDE CONTENT --------*/
*{
    padding: 0.1%;
    color: #ffffff;
    font-family: 'Exo 2', sans-serif;
}
* a{
    color: #ffffff;
}
* a:hover, * button:hover{
    text-decoration: none;
    color: #ff0000;
}
* img {
    max-width: 100%;
}
* iframe{
    width: 560px;
    height: 315px;
}
body{
    background-color: #1d1d1d;
    padding: 0.8rem;
}

.custom-header, .custom-footer{
    background-color: #1d1d1d;
}
h1 {
    font-family: 'Audiowide', cursive;
    font-size: 2.2rem;
}

/*-------- PROJECT SECTIONS --------*/
.projbar{
    background-color: #3d3d3d;
    padding-bottom: 2rem;
    padding-left: 3%;
    padding-right: 3%;
}
.projbar #title{
    margin-bottom: 1.2rem;
    font-size: 1.8rem;
}

/*-------- PROJECTS --------*/
.project{
    background-color: #000000;
    border: 1px solid #000000;
    border-radius: 8px;
    margin: 0.5rem;
    margin-top: 0%;
    min-width: 15rem;
    width: 26rem;
}
.wrapper {
    border: 5px outset #be0000;
    border-radius: 8px;
}
.project p{
    margin-top: 0.7em;
}
.project img{
    min-width: 220px;
}
.project:hover{
    box-shadow:0px 0px 0px 2px #797979 inset;
}

/*-------- ABOUT SECTION --------*/
.aboutbar{
    background-color: #2b2b2b;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.aboutbar p{
    font-size: 1.2rem;
}

/*-------- FOOTER --------*/
.custom-footer{
    text-decoration: none;
}
.custom-footer ul li{
    list-style-type: none;
    font-size: 1.8rem;
}

/*-------- MODAL --------*/
.modal-container {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    text-align: center;
}
.modal-content {
  width: 650px;
  height: 730px;
  border: 1px solid #be0000;
  background-color: #2b2b2b;
}
.modal-header{
    border: 0px;
    font-size: 2rem;
}
.modal-body p{
    margin-top: 1rem;
    text-align: left;
    max-height: 8rem;
    font-size: 1rem;
}
.modal-footer{
    border: 0px;
}
.modal-footer div{
    margin: 0%;
    width: 100%;
}
.modal-footer .link{
    float: left;
    text-align: left;
    font-size: 1rem;
}
.modal-footer .link .icon{
    margin-right: 0.5rem;
}
.modal-footer button{
    background-color: #000000;
    border: 1px solid #797979;
    padding: 0.5em;
    float: right;
}

/*-------- RESPONSIVE ADJUSTMENTS --------*/
@media only screen and (max-width: 1820px) {
    .project{
        width: 23rem;
    }
}
@media only screen and (max-width: 1620px) {
    .project{
        width: 20rem;
    }
}
@media only screen and (max-width: 1415px) {
    .project{
        width: 34rem;
    }
    .projgroup{
        justify-content: center;
    }
}
@media only screen and (max-width: 1190px) {
    .project{
        width: 28rem;
    }
}
@media only screen and (max-width: 1000px) {
    .project{
        width: 24rem;
    }
}
@media only screen and (max-width: 884px) {
    .project{
        width: 40rem;
    }
}
@media only screen and (max-width: 550px) {
    .modal-content{
        width: 100%;
        height: 100%;
    }
    iframe{
        width: 100%;
        height: 40%;
    }
    .modal-header{
        font-size: 18px;
    }
    .modal-body p, .modal-footer .link{
        font-size: 13px;
    }
    .modal-content .close-btn{
        font-size: 14px;
        margin-top: 10px;
    }
}

@media only screen and (max-height: 580px) {
    .modal-content{
        height: 100%;
    }
    .modal-body p, .modal-footer .link{
        font-size: 11px;
    }
    .modal-content .close-btn{
        font-size: 11px;
    }
    .modal-header{
        font-size: 16px;
        padding-bottom: 0%;
    }
    iframe{
        width: 100%;
        height: 60%;
    }
    .modal-content img{
      max-height: 60%;
    }
    .modal-body{
        height: 60%;
    }
}